import React, { useState } from 'react';
import './list.css';

function List() {
  const [people, setPeople] = useState([
    { id: 1, name: '', age: 20 },
    { id: 2, name: '', age: 22 },
    { id: 3, name: '', age: 21 },
    { id: 4, name: '', age: 19 },
    { id: 5, name: '', age: 23 },
    { id: 6, name: '', age: 24 },
    { id: 7, name: '', age: 18 },
    { id: 8, name: '', age: 20 },
    { id: 9, name: '', age: 21 }
  ]);

  const handleDelete = (id) => {
    setPeople(people.filter(person => person.id !== id));
  };

  return (
    <div className="list-container">
      <ul>
        {people.map((person) => (
          <li key={person.id}>
            {person.name} ({person.age} )
            <button onClick={() => handleDelete(person.id)}></button>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default List;
